}
body {
  color: rgb(17, 17, 17);
  margin: 0;
  overflow-x: hidden;
  min-height: 100vh;
}
footer {
  color: white !important;
  text-decoration: none !important;
}
form {
  margin-top: 200px;
  padding-left: 20%;
  padding-right: 20%;
}
td,
th {
  background-color: white !important;
}

.title {
  -webkit-text-decoration: underline pink !important;
          text-decoration: underline pink !important;
  font-family: "Merriweather Sans", sans-serif !important;
}
.fill img:hover {
  -webkit-filter: brightness(70%);
  filter: brightness(70%);
  transition: 0.5s;
}
h1 {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-family: "Merriweather Sans", sans-serif !important;
}
.recordImage {
  object-fit: contain;
}
i {
  font-size: 25px;
  color: grey;
  transition: 0.2s;
  font-family: "Font Awesome 5 Brands" !important;
}
.fas {
  font-family: "Font Awesome 5 Free" !important;
}
.fas:hover {
  color: rgb(70, 70, 70);
}
i:hover {
  color: rgba(168, 168, 168, 0.795);
  transition: 0.2s;
}
iframe {
  position: relative !important;
  height: 600px;
  width: 100%;
}
.fade {
  background-color: rgba(0, 0, 0, 0.678) !important;
}
.thumb:hover {
  transition: 0.3s;
  -webkit-filter: brightness(120%);
          filter: brightness(120%);
