.gradientContainer .gradient .gradient:after {
  min-height: 100%;
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(rgba(0, 0, 0, 0.678), transparent);
  z-index: 99;
}
.fullScreen {
  min-height: 100vh;
}
@media (max-width: 768px) {
  iframe {
    padding: 0;
    height: 400px;
  }
  .artistCard {
    height: 250px;
  }
}

header {
  z-index: 3;
}

header .navbar {
  position: absolute;
  top: 20px;
  right: 30px;
  background-color: rgba(119, 34, 34, 0) !important;
}

header .navbar .nav-link {
  color: white !important;
  font-size: 2rem;
  margin-left: 20px;
  font-family: "Merriweather Sans", sans-serif !important;
}

header .navbar .nav-link:hover {
  border-bottom: 4px solid pink;
  transition: 0s;
}

.logo {
  opacity: 0.9;
  position: absolute;
  z-index: 3;
  left: 40px;
  top: 40px;
  height: 200px;
  transition: 0.5s;
  width: auto;
}

.navbar-toggler {
  position: relative;
  transition: 0.5s;
  border-radius: 0%;
  border: none;
}

.navbar-toggler:focus,
.navbar-toggler:active {
  outline: none;
  box-shadow: none;
}

/* small screens */
@media screen and (max-width: 991px) {
  .logo {
    height: 85px;
    top: 10px;
    left: 10px;
  }

  .nav-link {
    background-color: black;
    text-align: center;
    text-shadow: none;
    border: 1px solid black;
    left: 30px;
    width: 270px;
  }
}
