.carouselContainer {
  height: 100vh;
  width: 100vw;
}

.carouselContainer .carousel {
  height: 100vh;
  overflow: hidden;
}

.carouselContainer .carousel .carousel-item img {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  object-position: 50% 50%;
  background-blend-mode: multiply;
}

.carouselContainer .carousel button {
  height: 0;
  visibility: hidden;
}

.carouselContainer .carousel .thumbs {
  visibility: hidden;
}

.carouselContainer .carousel .carousel:after {
  min-height: 100%;
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(rgba(0, 0, 0, 0.678), transparent);
}

.carouselContainer .carousel .legend {
  position: absolute;
  left: 300px;
  background-color: rgba(255, 192, 229, 0);
  opacity: 1;
  width: 200px;
  font-size: 1.4em;
  padding: 20px;
  border-radius: 0;
  -webkit-clip-path: polygon(0 1%, 100% 0, 100% 80%, 75% 100%, 0 100%, 0% 50%);
  clip-path: polygon(0 1%, 100% 0, 100% 80%, 75% 100%, 0 100%, 0% 50%);
}

.carouselContainer .carousel .control-dots {
  position: absolute;
  left: 40%;
}

.carouselContainer .carousel .carousel-status {
  visibility: hidden;
}

.carousel .slide img {
  object-fit: cover;
  height: 100vh;
  width: 100vw;
}

@media screen and (max-width: 768px) {
  .carousel-root {
    height: 200px;
  }
  .carousel {
    height: 200px;
  }
}
/*# sourceMappingURL=Carousel.css.map */


